import React, { useState } from 'react';
import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';
import { Container, Stack } from '@mui/material';
import Button from '@mui/material/Button';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';

const GoWasmComponent: React.FC = () => {
    const [jsonResult, setJsonResult] = useState("")
    const [yamlResult, setYamlResult] = useState("")

    const goJson2Yaml = () => {
        try {
            return setYamlResult((window.functions.json2Yaml!)(jsonResult))
        } catch (error) {
            console.log(error)
        }
    }

    const goYaml2Json = () => {

        try {
            return setJsonResult((window.functions.yaml2Json!)(yamlResult))
        } catch (error) {
            console.log(error)
        }
    }

    return (


        <div>
            <Box sx={{ flexGrow: 1 }}>
                <Grid container spacing={2}>
                    {/* Left Column */}
                    <Grid item xs={5}>
                        <TextField
                            fullWidth
                            variant="outlined"
                            multiline
                            rows={20}
                            placeholder="Type something..."
                            value={jsonResult}
                            onChange={e => setJsonResult(e.target.value)}
                        />
                    </Grid>

                    {/* Middle Column */}
                    <Grid item xs={1} container direction="column" alignItems="center" justifyContent="space-around">
                        <Button variant="contained" color="primary" onClick={goJson2Yaml}>
                            Json to Yaml
                        </Button>
                        <Button variant="contained" color="secondary" onClick={goYaml2Json}>
                            Yaml to Json
                        </Button>
                    </Grid>

                    {/* Right Column */}
                    <Grid item xs={5}>
                        <TextField
                            fullWidth
                            variant="outlined"
                            multiline
                            rows={20}
                            placeholder="Type something..."
                            value={yamlResult}
                            onChange={e => setYamlResult(e.target.value)}
                        />
                    </Grid>
                </Grid>
            </Box>
            {/* <Container fixed>
                <Stack spacing={3}>
                    <Stack spacing={2}>
                        <TextField value={jsonResult} label="JSON" variant="outlined" onChange={e => setJsonResult(e.target.value)} />
                        <button onClick={goJson2Yaml}>Json to Yaml</button>
                    </Stack>

                    <Stack spacing={2}>
                        <TextField value={yamlResult} label="YAML" variant="outlined" onChange={e => setYamlResult(e.target.value)} />
                        <button onClick={goYaml2Json}>Yaml to Json</button>
                    </Stack>
                </Stack>

            </Container> */}
        </div>
    );
}

export default GoWasmComponent;